import styles from './Navbar.module.css';
import {NavLink} from "react-router-dom";

function Navbar() {
    function isActiveLink() {
        return ({isActive}) => isActive ? styles.active : undefined
    }

    return (
        <nav className={styles.nav}>
            <ul>
                <li className={styles.item}>
                    <NavLink to="/profile" className={isActiveLink()}></NavLink>
                </li>
                <li className={styles.item}>
                    <NavLink to="/dialogs" className={isActiveLink()}></NavLink>
                </li>
                <li className={styles.item}>
                    <NavLink to="/friends" className={isActiveLink()}></NavLink>
                </li>
                <li className={styles.item}>
                    <NavLink to="/music" className={isActiveLink()}></NavLink>
                </li>
                <li className={styles.item}>
                    <NavLink to="/video" className={isActiveLink()}></NavLink>
                </li>
                <li className={styles.item}>
                    <NavLink to="/search" className={isActiveLink()}></NavLink>
                </li>
            </ul>
        </nav>
    );
}

export default Navbar;
